.upperHeader {
    align-items: center;
    box-sizing: border-box;
    color: rgb(34, 34, 34);
    display: flex;
    flex-shrink: 0;
    font-family: arial, sans-serif;
    font-size: 14px;
    height: 60px;
    padding-bottom: 6px;
    padding-left: 6px;
    padding-right: 6px;
    padding-top: 6px;
}

.middleHeader {
    color: rgb(0, 0, 0);
    display: block;
    font-family: Roboto, RobotoDraft, Arial, sans-serif;
    font-size: 13px;
    font-stretch: 100%;
    font-style: normal;
    font-variant-caps: normal;
    font-variant-east-asian: normal;
    font-variant-ligatures: normal;
    font-variant-numeric: normal;
    font-weight: 400;
    height: 48px;
    line-height: 27px;
    min-width: 0px;
    position: relative;
    transition-delay: 0s;
    transition-duration: 0.25s;
    transition-property: box-shadow;
    transition-timing-function: ease;
    /*width: 873px;*/
    width: 100%;
    z-index: 986;
}

.lowerHeader {
    align-items: center;
    box-sizing: border-box;
    color: rgb(0, 0, 0);
    display: flex;
    flex-basis: auto;
    flex-grow: 0;
    flex-shrink: 0;
    float: right;
    font-family: Roboto, RobotoDraft, Arial, sans-serif;
    font-size: 13px;
    font-stretch: 100%;
    font-style: normal;
    font-variant-caps: normal;
    font-variant-east-asian: normal;
    font-variant-ligatures: normal;
    font-variant-numeric: normal;
    font-weight: 400;
    height: 48px;
    justify-content: flex-end;
    line-height: normal;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 4px;
    padding-top: 0px;
    user-select: none;
    vertical-align: middle;
    white-space: nowrap;
    /* width: 253.266px; */
    width: 35%; /* Crutch №1! Remove after adding all the elements of the header, and comment out the annotated. Make adjustments to the circumstances */
    -webkit-box-align: center;
    -webkit-box-pack: start;
}

